import {
  formatAudioTime,
  seekPosition,
  setProgress,
} from './audio-player.utils';


class AudioPlayer extends HTMLAudioElement {
  static get is() {
    return 'audio-player';
  }

  createdCallback() {
    this.shadow = this.createShadowRoot();
    this.shadow.innerHTML = this.render();
  }

  get artist() {
    let artist = this.getAttribute('artist');
    return artist ? artist : null;
  }

  set artist(val) {
    this.setAttribute('artist', val);
  }

  get src() {
    let src = this.getAttribute('src');
    return src ? src : null;
  }

  set src(val) {
    this.setAttribute('src', val);
  }


  playOrPause() {
    const player = this.shadow.querySelector('audio');

    if (player.paused)
      player.play();

    return
      player.pause();
  }

  render() {
    return (`
    <style>
    </style>
      <div id="audio-player">
        <h1>${this.artist}</h1>
        <audio id="audio" src="${this.src}"></audio>
      </div>
    `)
  }
}

document.registerElement(AudioPlayer.is, AudioPlayer);



